var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'3. Innovación tecnológica y su papel en el desarrollo sustentable'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_c('p',{staticClass:"mb-4"},[_vm._v("La innovación tecnológica es fundamental para alcanzar los objetivos de desarrollo sustentable, porque proporciona soluciones avanzadas que permiten enfrentar los desafíos ambientales y sociales, a nivel global. En un contexto donde el cambio climático, la escasez de recursos naturales y la degradación de los ecosistemas, amenazan la estabilidad de los sistemas naturales y económicos, la tecnología emerge como una herramienta clave para mitigar estos problemas y promover un crecimiento más responsable. La innovación tecnológica aplicada a la sostenibilidad abarca desde el desarrollo de energías limpias hasta la optimización de procesos industriales, pasando por la gestión de residuos y el aprovechamiento de la digitalización para una toma de decisiones más precisa y eficiente.")]),_vm._m(0),_c('p',{staticClass:"mb-4"},[_vm._v("Las tecnologías para la sostenibilidad ambiental, representan una serie de innovaciones diseñadas para minimizar el impacto negativo de las actividades humanas en el medio ambiente y maximizar el uso responsable de los recursos naturales.")]),_c('div',{staticClass:"row mb-4"},[_vm._m(1),_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-left"}},[_vm._m(2),_c('p',{staticClass:"mb-4"},[_vm._v("Para conocer algunas de estas tecnologías, lo invitamos a escuchar el siguiente podcast.")]),_c('TarjetaAudio',{staticClass:"color-primario bg-white mb-0",attrs:{"texto":"Tecnologías para la sostenibilidad ambiental","tiempo":"","audio":require('../../assets/componentes/audios/Podcast_UnidadIII_Desarrollo sostenible_Tecnologías para la sostenibilidad ambiental.mp3')},on:{"audio-hover":function($event){_vm.mostrarIndicadorTarjetaAudio = false}}})],1)]),_vm._m(3),_c('p',{staticClass:"mb-4"},[_vm._v("La digitalización y las soluciones inteligentes están desempeñando un papel cada vez más relevante en la promoción de la sostenibilidad, dado que permiten una administración más precisa y eficiente de los recursos, una optimización de los procesos y una toma de decisiones basada en datos en tiempo real. El avance en tecnologías como la inteligencia artificial (IA), el Big Data, el Internet de las Cosas (IoT) y las plataformas digitales, ha transformado profundamente la gestión de procesos en sectores claves, como el energético, el agrícola, el urbano y el industrial, haciendo posible una mayor eficiencia y una reducción de desperdicios.")]),_c('p',{staticClass:"mb-4"},[_vm._v(" Conozcamos algunas de las soluciones inteligentes utilizadas para la sostenibilidad:")]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-right"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Inteligencia artificial - Big Data"}},[_c('p',[_vm._v("La inteligencia artificial y el "),_c('i',[_vm._v("Big Data")]),_vm._v(", permiten recopilar, analizar y procesar, grandes volúmenes de datos para identificar patrones y tendencias que optimizan el uso de recursos y la sostenibilidad. En el ámbito de la agricultura, por ejemplo, la IA permite analizar datos climáticos, de suelo y de crecimiento de cultivos para ajustar las prácticas agrícolas en función de las condiciones actuales. Esta tecnología ayuda a los agricultores a reducir el uso de agua, fertilizantes y pesticidas, minimizando su impacto ambiental y maximizando la eficiencia de producción. Además, el Big Data también se utiliza en el sector energético para predecir y gestionar la demanda de electricidad, lo cual permite optimizar el uso de las redes eléctricas y evitar el desperdicio de energía.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Internet de las Cosas (iot)"}},[_c('p',[_vm._v("El Internet de las Cosas (IoT) ha revolucionado la sostenibilidad en ciudades e industrias mediante la interconexión de dispositivos y sistemas que monitorean el uso de recursos y las condiciones ambientales en tiempo real. En las ciudades inteligentes, por ejemplo, los sensores IoT permiten gestionar el tráfico, reducir el consumo de energía en edificios y optimizar la recolección de residuos, contribuyendo a un entorno urbano más eficiente y menos contaminante. En el ámbito industrial, el IoT permite monitorizar el funcionamiento de las máquinas y los procesos de producción, detectando problemas y ajustando parámetros para reducir el consumo energético y mejorar la eficiencia. Estas aplicaciones son fundamentales para reducir el impacto ambiental de las operaciones industriales y mejorar la sostenibilidad de las ciudades, al reducir las emisiones y el consumo de recursos.")])])])],1),_vm._m(4)]),_c('div',{staticClass:"row mb-5"},[_vm._m(5),_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Plataformas digitales"}},[_c('p',[_vm._v("Las plataformas digitales y la gestión de datos, también juegan un papel clave en la sostenibilidad, puesto que permiten un monitoreo exhaustivo y transparente de los procesos y la trazabilidad en la cadena de suministro. A través de estas plataformas, las empresas pueden controlar sus procesos de producción y distribución, garantizando que se cumplan con los estándares de sostenibilidad y que se gestionen de manera ética. Las plataformas de trazabilidad y gestión de datos también facilitan el cumplimiento de normativas ambientales y la adopción de prácticas sostenibles, porque proporcionan a los consumidores información clara sobre el origen y la sostenibilidad de los productos que compran. De esta forma, las plataformas digitales no solo permiten a las empresas mejorar su desempeño ambiental, sino que también refuerzan la transparencia y la confianza de los consumidores en los productos sostenibles.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Ciudades inteligentes"}},[_c('p',[_vm._v("En el ámbito urbano, la digitalización ha permitido el desarrollo de ciudades inteligentes que integran tecnología para una administración óptima de los recursos y los servicios públicos. Las ciudades inteligentes aprovechan el análisis de datos en tiempo real para ajustar el tráfico, la gestión de la energía y el tratamiento de residuos, de acuerdo con las necesidades de los ciudadanos, promoviendo así un uso más racional de los recursos y mejorando la calidad de vida. Además, las ciudades inteligentes implementan sistemas de gestión ambiental que permiten monitorear y reducir las emisiones de carbono, gestionar el agua de manera sostenible y mejorar la eficiencia energética de los edificios, lo cual resulta esencial en la construcción de un futuro más sustentable.")])])])],1)]),_c('div',{staticClass:"bg-full-width border-top-9-azul"},[_c('div',{staticClass:"px-5 pt-5 pb-0"},[_c('h2',[_vm._v("Material complementario")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-8"},[_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3 mb-auto mt-1",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/book.svg")}}),_c('a',{staticClass:"text-small text-primary",attrs:{"target":"_blank","href":"https://elibro.net/es/lc/tecnologicadeloriente/titulos/172965"}},[_vm._v("Productos y servicios inteligentes y sostenibles: técnicas para la innovación y la creatividad - Guilera, L. & Garrell, A.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3 mb-auto mt-1",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/book.svg")}}),_c('a',{staticClass:"text-small text-primary",attrs:{"target":"_blank","href":"https://elibro.net/es/lc/tecnologicadeloriente/titulos/174448"}},[_vm._v("La tec